@import url(https://fonts.googleapis.com/css?family=Libre+Baskerville);
html {
    background-color: rgb(255, 255, 255);
    font-family: 'Libre Baskerville', serif;
}
h1 {
    padding: 20px;
}
.gallery {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    max-width: 1300px;
    padding: 0 50px;
    margin: 0 auto;
    -webkit-column-count: 1;
            column-count: 1;
    -webkit-column-gap: 0;
            column-gap: 0;
    font-size: 0px;
}
gallery img {
    width: 100%;
}
@media all and (min-width: 768px){
.gallery {
    -webkit-column-count: 3;
            column-count: 3;
    margin: 50px auto;
}
}
@media all and (min-width: 1200px){
.gallery {
    -webkit-column-count: 4;
            column-count: 4;
    margin: 50px auto;
}
}
.thumbnail {
    width: 100%;
    -webkit-transition: 0.5s ease-out;
    transition: 0.5s ease-out;
}
.thumbnail:hover {
    cursor: pointer;
    -webkit-transform: scale(.95);
            transform: scale(.95);
}
.lightbox {
    z-index: 10;
    position: fixed;
    top: 0; bottom: 0; left: 0; right: 0;
    background-color: rgba(0,0,0,.7);
    font-size: 16px;
}
.lbxImg {
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translateY(-50%) translateX(-50%);
            transform: translateY(-50%) translateX(-50%);
    max-height: 80vh;
    max-width: 60vw;
}
.fa-times-circle {
    position: absolute;
    top: 1%; right: 1%;
    color: white;
}
.fa-times-circle,
.fa-angle-left,
.fa-angle-right {
    cursor: pointer;
}
.fa-angle-left, .fa-angle-right {
    position: absolute;
    top: 50%;
    color: white;
}
.fa-angle-left {
    left: 5%;
}
.fa-angle-right {
    right: 5%;
}

